import { CurrencyMaskModule } from 'ng2-currency-mask';
import { NgModule, LOCALE_ID } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { SelectModule } from 'ng2-select';
import { LaddaModule } from 'angular2-ladda';
import { ModalModule } from 'ng2-bootstrap/modal';

import { SharedComponentModule } from './../../shared/shared-component.module';

import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { PedidoRoutingModule } from './pedido-routing.module';

import { TextMaskModule } from 'angular2-text-mask';
import { TabsModule } from 'ng2-bootstrap/tabs';

import { PedidoService } from './pedido.service';
import { StorageService } from './../../../services/storage.service';
import { ClienteModule } from './../cadastros/cliente/cliente.module';
import { FormaPagamentoService } from '../config/formapagamento/forma-pagamento.service';
import { ServicoEntregaService } from '../config/servicoentrega/servico-entrega.service';
import { TaxaEntregaService } from './../config/taxaentrega/taxa-entrega.service';

import { ListarPedidoComponent } from './listar/listar-pedidocomponent';
import { CreatePedidoComponent } from './create/create-pedido.component';
import { UpdatePedidoComponent } from './update/update-pedido.component';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    HttpClientModule,
    PopupModalModule,
    TabsModule,
    PedidoRoutingModule,
    LaddaModule,
    TextMaskModule,
    ClienteModule,
    TextMaskModule,
    CurrencyMaskModule
  ],
  declarations: [
    ListarPedidoComponent,
    CreatePedidoComponent,
    UpdatePedidoComponent
  ],
  providers:[
    PedidoService,
    FormaPagamentoService,  
    TaxaEntregaService,
    ServicoEntregaService,
    StorageService
  ]
})
export class PedidoModule { }



