import { PedidoDTO } from 'models/domain/pedido.dto';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Location } from '@angular/common';

import { AbstractPedidoComponent } from '../abstract-pedido.component';

import { PedidoService } from './../pedido.service';
import { TaxaEntregaService } from './../../config/taxaentrega/taxa-entrega.service';
import { FormaPagamentoService } from './../../config/formapagamento/forma-pagamento.service';
import { ServicoEntregaService } from '../../config/servicoentrega/servico-entrega.service';
import { TrataErrorService } from 'services/trata-error.service';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';

@Component({
  templateUrl: './update-pedido.component.html',
  styleUrls: ['./update-pedido.component.scss']
})
export class UpdatePedidoComponent extends AbstractPedidoComponent implements OnInit {



  constructor(public fb: FormBuilder, public pagService: FormaPagamentoService, public taxaService: TaxaEntregaService,
    public servicoService: ServicoEntregaService, public pedidoService: PedidoService,
    public location: Location, public router: Router, public route: ActivatedRoute) {
    super(fb, pagService, taxaService, servicoService, pedidoService, location, router);
  }

  ngOnInit() {
    this.criarFormulario();
    this.limpar();
    this.carregarCombos();


    this.route.params.forEach((params: Params) => {
      let id = params['id'];
      this.pedidoService.findById(id).subscribe(
        resp => {
          this.pedido = resp as PedidoDTO;
          this.atualizarFormulario(this.pedido);
        }
        , error => TrataErrorService.tratarError(error)
      )
    });
  }


  confirmar(pedidoForm: any) {
    this.preencherPedido(pedidoForm);
    this.pedidoService.atualizar(this.pedido)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Pedido', 'Pedido alterado com sucesso!!!');
        this.router.navigate(['/pedidos/listar']);
      }, error => TrataErrorService.tratarError(error))

  }


  private atualizarFormulario(pedido: PedidoDTO) {
    this.exibirProduto = true;
    this.exibirDadosPedido = true;
    this.enderecos = pedido.cliente.enderecos;
    this.formulario.patchValue({
      id: pedido.id,
      cliente: {
        id: pedido.cliente.id,
        nmPessoa: pedido.cliente.nmPessoa
      },
      taxaEntrega: pedido.taxaEntrega.id,
      formaPagamento: pedido.formaPagamento.id,
      vlDesconto: pedido.vlDesconto
    });
    if (pedido.enderecoEntrega !=null) {
      this.formulario.patchValue({
        enderecoEntrega: pedido.enderecoEntrega.id,
      })
    }
    if (pedido.taxaEntrega.vlTaxaEntrega > 0) {
      this.exibirServicoEntrega =  true;
      this.formulario.patchValue({
        servicoEntrega: pedido.servicoEntrega.id
      })
    }
    this.totalizarPedido();
  }

}



