import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ListarPedidoComponent } from './listar/listar-pedidocomponent';
import { CreatePedidoComponent } from './create/create-pedido.component';
import { UpdatePedidoComponent } from './update/update-pedido.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'listar',
    pathMatch: 'full',
  },
  {
    path: '',
    data: {
      title: 'Pedidos'
    },
    children: [
      {
        path: 'listar',
        component: ListarPedidoComponent,
        data: {
          title: 'Listar'
        }
      },
      {
        path: 'novo',
        component: CreatePedidoComponent,
        data: {
          title: 'novo pedido'
        }
      },
      {
        path: 'alterar/:id',
        component: UpdatePedidoComponent,
        data: {
          title: 'alterar pedido'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PedidoRoutingModule { }



