import { PedidoDTO } from './../../../../models/domain/pedido.dto';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { Location } from '@angular/common';

import { AbstractPedidoComponent } from '../abstract-pedido.component';

import { PedidoService } from './../pedido.service';
import { TaxaEntregaService } from './../../config/taxaentrega/taxa-entrega.service';
import { FormaPagamentoService } from './../../config/formapagamento/forma-pagamento.service';
import { ServicoEntregaService } from '../../config/servicoentrega/servico-entrega.service';
import { TrataErrorService } from 'services/trata-error.service';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';

@Component({
  templateUrl: './create-pedido.component.html',
  styleUrls: ['./create-pedido.component.scss']
})
export class CreatePedidoComponent extends AbstractPedidoComponent implements OnInit {



  constructor(
    public fb: FormBuilder,
    public pagService: FormaPagamentoService,
    public taxaService: TaxaEntregaService,
    public servicoService: ServicoEntregaService,
    public pedidoService: PedidoService,
    public location: Location,
    public router: Router) {
    super(fb, pagService, taxaService, servicoService, pedidoService, location, router);
  }

  ngOnInit() {
    this.criarFormulario();
    this.carregarCombos();
    this.limpar();
  }



  confirmar(pedidoForm: any) {
    this.preencherPedido(pedidoForm);
    this.pedidoService.salvar(this.pedido)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Pedido', 'Pedido incluído com sucesso!!!');
        this.router.navigate(['/pedidos/listar']);
      },
        error => TrataErrorService.tratarError(error))
  }
}



