import { TrataErrorService } from 'services/trata-error.service';
import { PedidoService } from './../pedido.service';
import { ClienteService } from './../../cadastros/cliente/cliente.service';
import { PedidoDTO } from 'models/domain/pedido.dto';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { API_CONFIG } from 'config/core/api.config';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { ProdutoDTO } from 'models/domain/produto.dto';

@Component({
  templateUrl: './listar-pedido.component.html',
  styleUrls: ['./listar-pedido.component.scss']
})
export class ListarPedidoComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('excluirModal') popupExclusao;
  @ViewChild('changeStatusModal') popupChangeStatus;

  pedido: PedidoDTO;
  items: PedidoDTO[] = [];

  constructor(
      public clienteService: ClienteService, 
      public pedidoService: PedidoService,
      public router: Router) {
    super();
  }

  ngOnInit() {
    this.carregarTodos()
  }

  selecionarItem(item: PedidoDTO) {
    this.pedido = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: PedidoDTO) {
    this.pedido = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.pedidoService.changeStatus(this.pedido)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Pedido', 'Status do pedido foi alterado com sucesso!!!')
        this.carregarTodos();
      }, error => TrataErrorService.tratarError(error));
  }

  alterarPedido(pedido: PedidoDTO) {
    this.router.navigate([`pedidos/alterar/${pedido.id}`])
  }


  excluir() {
    this.pedidoService.excluir(this.pedido)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Pedido', 'Pedido foi excluído com sucesso!!!')
        this.carregarTodos();
      }, error => TrataErrorService.tratarError(error));
  }

  private carregarTodos() {
    this.pedidoService.findAll().subscribe(
      res => {this.items = res},
      error => TrataErrorService.tratarError(error));
  }

}



