import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';


import { AbstractBaseComponent } from 'config/core/abstract-base.component';

import { ServicoEntregaDTO } from './../../../models/domain/servico-entrega.dto';
import { TaxaEntregaDTO } from './../../../models/domain/taxa-entrega.dto';
import { FormaPagamentoDTO } from './../../../models/domain/forma-pagamennto.dto';
import { EnderecoDTO } from './../../../models/domain/endereco.dto';
import { PedidoDTO } from 'models/domain/pedido.dto';
import { PedidoItemDTO } from './../../../models/domain/pedido-item.dto';
import { ClienteDTO } from './../../../models/domain/cliente.dto';

import { PedidoService } from './pedido.service';
import { ServicoEntregaService } from './../config/servicoentrega/servico-entrega.service';
import { TaxaEntregaService } from './../config/taxaentrega/taxa-entrega.service';
import { FormaPagamentoService } from './../config/formapagamento/forma-pagamento.service';
import { TrataErrorService } from 'services/trata-error.service';

export abstract class AbstractPedidoComponent extends AbstractBaseComponent {

  pedido: PedidoDTO;
  exibirProduto: boolean = false;
  exibirDadosPedido = false;
  exibirServicoEntrega = false;

  enderecos: EnderecoDTO[];
  formaPagmentos: FormaPagamentoDTO[] = [];
  taxaEntregas: TaxaEntregaDTO[] = [];
  servicoEntregas: ServicoEntregaDTO[] = [];


  constructor(
    public fb: FormBuilder,
    public pagService: FormaPagamentoService,
    public taxaService: TaxaEntregaService,
    public servicoService: ServicoEntregaService,
    public pedidoService: PedidoService,
    public location: Location,
    public router: Router) {
    super();
  }

  voltar() {
    this.location.back();
  }

  limpar() {
    this.exibirServicoEntrega = false;
    this.exibirDadosPedido = false;
  }

  totalItem(item: PedidoItemDTO): number {
    return item.qtItem * item.produto.vlVenda;
  }

  decreaseQuantity(item: PedidoItemDTO) {
    item.qtItem--
    if (item.qtItem == 0) {
      this.removeItem(item);
    }
    this.totalizarPedido();
  }

  increaseQuantity(item: PedidoItemDTO) {
    item.qtItem++
    this.totalizarPedido();
  }

  removeItem(item: PedidoItemDTO) {
    this.pedido.itens = this.pedido.itens.filter((it: PedidoItemDTO) => it.hash != item.hash);
    this.totalizarPedido();
  }

  atualizarValorDesconto(desconto: string){
    this.pedido.vlDesconto = Number(desconto.replace(',',"."));
    this.totalizarPedido();
  }

  totalizarPedido() {
    this.pedido.vlPedido = 0;
    if (this.pedido.itens.length > 0) {
      for (let item of this.pedido.itens) {
        let total = item.qtItem * item.produto.vlVenda;
        this.pedido.vlPedido += total;
      }
      this.pedido.vlPedido += this.pedido.vlTaxaEntrega - this.pedido.vlDesconto;
      this.exibirDadosPedido = true;
    } else {
      this.limpar();
    }
  }

  selecionarTaxaEntrega(te: any) {
    this.exibirServicoEntrega = !(te == 1);
    if (this.exibirServicoEntrega) {
      let position = this.taxaEntregas.findIndex(x => x.id == te);
      this.pedido.vlTaxaEntrega = this.taxaEntregas[position].vlTaxaEntrega;
      this.exibirServicoEntrega = true;
    } else {
      this.pedido.vlTaxaEntrega = 0;
      this.exibirServicoEntrega = false;
    }
    this.totalizarPedido();
  }

  selecionarCliente(cliente: ClienteDTO) {
    this.exibirProduto = true;
    this.pedido.cliente = cliente;
    this.formulario.patchValue({
      cliente: {
        id: cliente.id,
        nmPessoa: cliente.nmPessoa
      },
      vlDesconto:['']
    })
    this.pedido.itens = []
    this.enderecos = cliente.enderecos;
  }


  adicionarProduto(pedidoItem: PedidoItemDTO) {
    let position = this.pedido.itens.findIndex(x => x.hash == pedidoItem.hash);
    if (position == -1) {
      this.pedido.itens.push(pedidoItem);
    } else {
      this.pedido.itens[position].qtItem++;
    }
    this.totalizarPedido();
  }

  protected criarFormulario() {
    this.exibirProduto = false;
    this.pedido = {} as PedidoDTO;
    this.pedido.itens = [] as PedidoItemDTO[];
    this.pedido.emAlteracao = false;
    this.pedido.vlTaxaEntrega = 0;
    this.pedido.vlDesconto = 0;
    this.pedido.status = 'E'
    this.formulario = this.fb.group({
      id: [''],
      cliente: this.fb.group({
        id: [''],
        nmPessoa: ['']
      }),
      vlDesconto:[''],
      enderecoEntrega: ['1'],
      taxaEntrega: ['1'],
      servicoEntrega: ['1'],
      formaPagamento: ['1']
    })
  }


  carregarCombos() {
    this.pagService.findAllAtivo().subscribe(res => this.formaPagmentos = res, error => TrataErrorService.tratarError(error));
    this.taxaService.findAllAtivo().subscribe(res => this.taxaEntregas = res, error => TrataErrorService.tratarError(error));
    this.servicoService.findAllAtivo().subscribe(res => this.servicoEntregas = res, error => TrataErrorService.tratarError(error));
  }


  protected preencherPedido(pedidoForm: any) {
    this.pedido.id = pedidoForm.id;
    this.pedido.cliente = {} as ClienteDTO;
    this.pedido.cliente.id = pedidoForm.cliente.id;
    this.pedido.vlDesconto = pedidoForm.vlDesconto;

    this.pedido.enderecoEntrega = {} as EnderecoDTO;
    this.pedido.enderecoEntrega.id = pedidoForm.enderecoEntrega;

    this.pedido.taxaEntrega = {} as TaxaEntregaDTO;
    this.pedido.taxaEntrega.id = pedidoForm.taxaEntrega;
    if (pedidoForm.servicoEntrega != undefined) {
      this.pedido.servicoEntrega = {} as ServicoEntregaDTO;
      this.pedido.servicoEntrega.id = pedidoForm.servicoEntrega;
    }
    this.pedido.formaPagamento = {} as FormaPagamentoDTO;
    this.pedido.formaPagamento.id = pedidoForm.formaPagamento;
    this.pedido.origem = 'W'
  }

}



